  padding:20px;
  border-radius:40px;
  transition:0.5s;
  }
  
  .contact button:hover{
  background:#70c48b;
  }
  
  footer{
  color:#000;
  margin-top:2vh;
  margin-left:2vw;
  }
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
